body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.node {
    width: 25px;
    height: 25px;
    outline: 1px solid rgb(175, 174, 174);
    display: inline-block;
  }
  
  .node-finish {
    -webkit-animation-name: finishNodeAnimation;
            animation-name: finishNodeAnimation;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
    background-image: url(/pathfinding-visualizer/static/media/finish.68b39c65.svg);
    background-size: 25px 25px;
  }
    
  @-webkit-keyframes finishNodeAnimation{
    0% {
      background-color: white;
    }
    60% {
      -webkit-transform: scale(1.05);
              transform: scale(1.05);
    }
    100%{
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color:rgb(231, 231, 163);
      
    }
  }
    
  @keyframes finishNodeAnimation{
    0% {
      background-color: white;
    }
    60% {
      -webkit-transform: scale(1.05);
              transform: scale(1.05);
    }
    100%{
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color:rgb(231, 231, 163);
      
    }
  }
  
  .node-start {
    -webkit-animation-name: startNodeAnimation;
            animation-name: startNodeAnimation;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
    background-image: url(/pathfinding-visualizer/static/media/start.e316a6de.svg);
    background-size: 25px, 25px;
  }
  
  @-webkit-keyframes startNodeAnimation{
    0% {
      background-color: white;
    }
    75% {
      -webkit-transform: scale(1.05);
              transform: scale(1.05);
    }
    100%{
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgb(227, 228, 159);
    }
  }
  
  @keyframes startNodeAnimation{
    0% {
      background-color: white;
    }
    75% {
      -webkit-transform: scale(1.05);
              transform: scale(1.05);
    }
    100%{
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgb(227, 228, 159);
    }
  }
  
  .node-wall {
    -webkit-animation-name: wallNodeAnimation;
            animation-name: wallNodeAnimation;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
  }
  
  @-webkit-keyframes wallNodeAnimation{
    0% {
      -webkit-transform: scale(0.9);
              transform: scale(0.9);
      background-color: rgb(219, 214, 214);
    }
    75% {
      -webkit-transform: scale(1.15);
              transform: scale(1.15);
    }
    100%{
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgb(126, 126, 121);
    }
  }
  
  @keyframes wallNodeAnimation{
    0% {
      -webkit-transform: scale(0.9);
              transform: scale(0.9);
      background-color: rgb(219, 214, 214);
    }
    75% {
      -webkit-transform: scale(1.15);
              transform: scale(1.15);
    }
    100%{
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgb(126, 126, 121);
    }
  }
  
  .node-visited {
    -webkit-animation-name: visitedAnimation;
            animation-name: visitedAnimation;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
  }
  
  @-webkit-keyframes visitedAnimation {
    0% {
      -webkit-transform: scale(0.3);
              transform: scale(0.3);
      border-radius: 50%;
      outline: 0;
      background-color: rgba(255, 0, 191, 0.5);
      border-radius: 100%;
    }
    50% {
      outline: 0;
      background-color:  rgba(255, 0, 191, 0.5);
    }
  
    60% {
      -webkit-transform: scale(1);
              transform: scale(1);
      outline: 1px;
      background-color: rgba(4, 0, 255, 0.5);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color:rgba(4, 0, 255, 0.5);
    }
  }
  
  @keyframes visitedAnimation {
    0% {
      -webkit-transform: scale(0.3);
              transform: scale(0.3);
      border-radius: 50%;
      outline: 0;
      background-color: rgba(255, 0, 191, 0.5);
      border-radius: 100%;
    }
    50% {
      outline: 0;
      background-color:  rgba(255, 0, 191, 0.5);
    }
  
    60% {
      -webkit-transform: scale(1);
              transform: scale(1);
      outline: 1px;
      background-color: rgba(4, 0, 255, 0.5);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color:rgba(4, 0, 255, 0.5);
    }
  }
  
  .node-path {
    -webkit-animation-name: shortestPath;
            animation-name: shortestPath;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
  }
  
  @-webkit-keyframes shortestPath {
    0% {
      -webkit-transform: scale(0.6);
              transform: scale(0.6);
      background-color: rgb(226, 184, 137);
    }
  
    50% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      background-color: rgb(214, 117, 7);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgb(230, 214, 170);
    }
  }
  
  @keyframes shortestPath {
    0% {
      -webkit-transform: scale(0.6);
              transform: scale(0.6);
      background-color: rgb(226, 184, 137);
    }
  
    50% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      background-color: rgb(214, 117, 7);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgb(230, 214, 170);
    }
  }
.grid-container {
  margin: 10% auto;
  overflow-x: auto;
}

.grid {
  table-layout: fixed;
}

.row {
  display: flex;
  flex-wrap: nowrap;
}

.navbar-brand {
  margin-left: 20px;
}

.nav-link.visualize-btn {
  float: right; 
  border: 2px solid #007bff; 
  color: #007bff; 
  background-color: transparent; 
  border-radius: 5px; 
  padding: 5px 15px;
  transition: background-color 0.3s, color 0.3s;
}

.nav-link.visualize-btn:hover {
  background-color: #007bff;
  color: #ffffff;
}

.nav-link:hover,
.dropdown-item:hover {
  background-color: #e9ecef;
  color: #000;
}

.modal-backdrop {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

.modal-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  width: 70%;
  max-width: 600px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
}

.close-button {
  width: auto; 
  padding: 5px 15px;
  margin-top: 20px;
  display: block; 
  margin-left: auto;
  margin-right: auto;
}

